export default {
	data() {
		return {
			isLogin: false
		};
	},
	onShow() {
		this.isLogin = uni.getStorageSync('access_token')
	},
	methods:{
		showDialog() {
		  uni.showModal({
		      title: '提示',
		      content: '是否退出当前账号?',
			  confirmText: '退出',
		      success: function (res) {
		          if (res.confirm) {
		              let records      = uni.getStorageSync('records')
					  let platform     = uni.getStorageSync('platform')
					  let dialogIsShow = uni.getStorageSync('dialogIsShow')
		              uni.clearStorageSync() // 清除缓存
					  uni.setStorageSync('records',records)
					  uni.setStorageSync('platform',platform)
					  uni.setStorageSync('dialogIsShow',dialogIsShow)
		              uni.switchTab({
		              	url:'/pages/user/user'
		              })
		          } else if (res.cancel) {
		              console.log('用户点击取消');
		          }
		      }
		  });
		},
		goProtocal(){
			uni.navigateTo({
				url:'/pages/user/protocal/protocal'
			})
		},
		goAbout(){
			uni.navigateTo({
				url:'/pages/user/about/about'
			})
		}
	}
}
